import { Injector } from '@angular/core';
import { RxViewDesignerActionModel } from '@helix/platform/view/designer';
import { TextFormControlComponent } from '@helix/platform/shared/components';
import { IViewActionDesignPropertyEditorConfig, IViewActionDesignSandbox } from '@helix/platform/view/api';
import { IPlainObject } from '@helix/platform/shared/api';
import { take } from 'rxjs/operators';
import { keys } from 'lodash';

export class RxUnknownViewActionDesignModel extends RxViewDesignerActionModel {
  constructor(protected injector: Injector, readonly sandbox: IViewActionDesignSandbox) {
    super(injector, sandbox);

    this.sandbox.actionProperties$.pipe(take(1)).subscribe((actionProperties: IPlainObject) => {
      this.sandbox.setActionPropertyEditorConfig(this.getActionEditorConfig(actionProperties));
    });
  }

  private getActionEditorConfig(actionProperties: IPlainObject): IViewActionDesignPropertyEditorConfig {
    return keys(actionProperties).map((propertyName) => ({
      name: propertyName,
      component: TextFormControlComponent,
      isDisabled: true,
      options: {
        label: propertyName
      }
    }));
  }
}
